::-webkit-input-placeholder {
  color: #fff;
}

.section {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;

  background: url("../../Assets/Images/blurred-minimalism-gradient-wallpaper-preview.jpeg")
    no-repeat;
  background-position: center;
  background-size: cover;
}
.formBox {
  position: relative;
  width: 400px;
  height: 450px;
  background: transparent;
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-radius: 20px;
  backdrop-filter: blur(15px);
  display: flex;
  justify-content: center;
  align-items: center;
}
.h2 {
  font-size: 2em;
  color: #fff;
  text-align: center;
}
.inputBox {
  position: relative;
  margin: 30px 0;
  width: 310px;
  /* border-bottom: 2px solid #fff; */
}
.inputBox label {
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
  color: #fff;
  font-size: 1em;
  pointer-events: none;
  transition: 0.5s;
}
.inputBox input:focus ~ label,
.inputBox input:valid ~ label {
  top: -5px;
}
.inputBox input {
  width: 100%;
  height: 50px;
  background: transparent;
  border: none;
  outline: none;
  font-size: 1em;
  padding: 0 35px 0 5px;
  color: #fff;
}
.inputBox ion-icon {
  position: absolute;
  right: 8px;
  color: #fff;
  font-size: 1.2em;
  top: 20px;
}
.forget {
  margin: -15px 0 15px;
  font-size: 0.9em;
  color: #fff;
  display: flex;
  justify-content: space-between;
}

.forget label {
  display: grid;
  grid-template-columns: 25px 1fr;
}

.forget label input {
  border: none;
  margin-right: 3px;
}

.forget label a {
  color: #fff;
  text-decoration: none;
  margin-left: 5px;
}
.forget label a:hover {
  text-decoration: underline;
}
.btn {
  width: 100%;
  height: 40px;
  border-radius: 40px;
  background: #fff;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 1em;
  font-weight: 600;
}
.register {
  font-size: 0.9em;
  color: #fff;
  text-align: center;
  margin: 25px 0 10px;
}
.register p a {
  text-decoration: none;
  color: #fff;
  font-weight: 600;
}
.register p a:hover {
  text-decoration: underline;
}
